import axios from "axios"

export default class Config {
    config
    url
    constructor() {
        if (!this.url)
            this.init()
    }

    async init() {
        await this.fetchConfig()
        this.makeUrl()
    }
    async fetchConfig() {
        this.config = await axios.get("/config.json", {
            dataType: "application/json"
        })
    }

    makeUrl() {
        this.url = window.location.protocol + "//" + window.location.hostname + ":" + this.config.PORT + "/api"
        this.placesUrl = `${this.url}/user/places`
        this.googleApiNearby = `${this.url}/google-api/nearby-search`
        this.googleApiDetails = `${this.url}/google-api/details`
        console.log(this.url)
    }
}
